body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  box-sizing: border-box;
  cursor: default;
  color: #333; }

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

body {
  font-size: .875em;
  font-family: 'Source Code Pro', monospace, sans-serif;
  font-weight: 400;
  box-sizing: inherit; }

#root {
  width: 100vw;
  height: 100vh; }

.App {
  text-align: center;
  display: grid;
  grid-template-rows: repeat(5, 1fr);
  width: 95vw;
  height: 100vh;
  -webkit-align-items: center;
          align-items: center;
  padding: 1rem;
  background-color: ghostwhite;
  box-shadow: 0px 0px 15px 1px rgba(100, 100, 100, 0.3);
  margin: 0 auto; }

.NumberForm {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row; }
  .NumberForm input {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    font-size: 2rem;
    padding: .1rem 0.2rem;
    color: #777; }
  .NumberForm label {
    color: #888;
    width: 20vw;
    min-width: 226px;
    font-size: 2rem;
    padding: .5rem 1rem; }

