:root{
  box-sizing: border-box;
  cursor: default;
  color: #333;
}

*,
::after,
::before{
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}


body {

  font-size: .875em;
  font-family: 'Source Code Pro', monospace, sans-serif;
  font-weight: 400;
  box-sizing: inherit;
}

#root{
  width: 100vw;
  height: 100vh;
}

.App {
  text-align: center;
  display: grid;
  grid-template-rows:  repeat(5,  1fr);
  width: 95vw;
  height: 100vh;
  align-items: center;
  padding: 1rem;
  background-color: ghostwhite;
  box-shadow: 0px 0px 15px 1px rgba(100,100,100,.3);
  margin: 0 auto;
}

.NumberForm{
  display: flex;
  //background-color: antiquewhite;
  flex-direction: row;
  input{
    //width: 100%;
    flex-grow: 1;
    font-size: 2rem;
    padding: .1rem 0.2rem;
    color: #777;
  }
  label{
    //flex-grow: 1;
    color: #888;
    width: 20vw;
    min-width: 226px;
    font-size: 2rem;
    padding: .5rem 1rem;
  }
}